                        </li>
                        <li class="process-step-copyWriter">
                            <p>登录阅读</p>
                            <p>各得4元</p>
                        </li>
                    </div>
                </div>
            </div>
            <div
                :class="['activityRule',suspendShareBtn?'suspendShareRule':'']"
                id='activityRule'>
                <div class="activityRule-title"/>
                <div class="activityRule-content">
                    <div
                        v-for="(item,index) in activityRule"
                        :key='index'
                        class="activityRule-content-item"
