                    />
                </div>
            </div>
            <div
                :class="['shareBtn',suspendShareBtn?'suspendShareBtn':'']"
                @click="share">
                <div class="shareImg"/>
                点击分享给微信好友
            </div>

            <GrayMask
                :show="ShowReceivePacketModal">
                <div
                    :class="[bounceLeave?'bounceLeave':'','receivePacketModal']">
                    <div
                        @click="closeReceiveModal"
                        class='facePrizeClose'/>
                    <div
                        @click="openPacket"
                        class='open'/>
                </div>
            </GrayMask>
            <GrayMask
