                        @click="depositAccount"/>
                    <div class="iconInfo">
                        <p>提示好友下载想看并登录</p>
                        <p>你可继续得第二份4元现金红包</p>
                    </div>
                </div>
            </GrayMask>
            <GrayMask
                :show="showToastModal">
                <div class="toastModal">
                    <div
                        @click="closeModal('showToastModal')"
                        class='facePrizeClose'/>
                    <div class="toastModalMsg">
                        <p>{{ toastMsg.title }}</p>
                        <p>{{ toastMsg.msg || '该扫码好友信息异常' }}</p>
                    </div>
                    <div class="toastModalFooter">
                        <p>{{ toastMsg.hint }}</p>
                    </div>
                </div>
            </GrayMask>
        </div>
    </div>

</template>

<script>
import QRious from 'qrious';
import NavBar from '@/components/NavBar';
import GrayMask from '@/components/GrayMask';
import { isXiangkan } from '@/utils/ua';
import { shareCodeTextToWechat } from '@/utils/share';
import { getElementsByClassName } from '@/utils/dom';
import { f2fHome, f2fPacket, refresh } from '@/api/invitation';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { listenScrollMointorDomInView } from '@/utils/scroll';
import { getUserAccountInfo, isTakeOverBackByWeb, doLogin } from '@/utils/jsApiV2/client';
import * as relativeConfig from './config';

export default {
    name: 'InvitationFace',
    components: {
        NavBar,
        GrayMask,
    },
    props: {
        // eslint-disable-next-line vue/require-default-prop
        code: String,
    },
    data() {
        return {
            userInfo: {},
            qr: null,
            activityRule: relativeConfig.faceTofaceRule,
            totalStep: [1, 2, 3, 4],
            step: 0,
            computeWidth: 0,
            remain: [],
            carousel: [],
            token: '',
            timer: null,
            key: '',
            ShowReceivePacketModal: false,
            ShowGoldIconModal: false,
            bounceLeave: false,
            coin: 666,
            suspendShareBtn: false,
            showToastModal: false,
            toastMsg: '',
            invitationCode: '',
        };
    },
    created() {
        this.userInfo = getUserAccountInfo();
        this.$root.hidePlaceholder();
        this.getHomeInfo();
        this.$o2o({
            e_c: '面对面邀请',
            e_a: '曝光',
            e_n: '面对面详情页曝光',
        });
        addCustomEventListener('scanQrmessage', this.scanQrCode);
    },
    mounted() {
        // 接管原生back回退键的操作
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
        this.monitorQRious();
    },
    destroyed() {
        clearInterval(this.timer);
        // 释放原生事件
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        clickBackHandler() {
            if (!this.code) {
                this.$router.push('/act/invitation');
            }
        },
        backKeyHandler() {
            this.$router.push('/act/invitation');
        },
        monitorQRious() {
            listenScrollMointorDomInView({
                dom: window,
                targetDom: getElementsByClassName('qr')[0],
                customMonitorFn: this.customMonitorFn,
                callback: this.showShareBtn,
                keepAlive: true,
            });
        },
        customMonitorFn() {
            const dom = getElementsByClassName('qr')[0];
            let top = 0;
            let height = 0;
            if (dom) {
                top = dom.getBoundingClientRect().top;
                height = dom.clientHeight;
            }
            if (top < 0 && (Math.abs(top) >= height)) {
                return true;
            }
            if (this.suspendShareBtn) {
                this.suspendShareBtn = false;
            }
            return false;
        },
        showShareBtn() {
            if (!this.suspendShareBtn) {
                this.suspendShareBtn = true;
            }
        },
        computeDomWidth() {
            const dom = getElementsByClassName('process-step')[0];
            const singleWidth = (dom && dom.clientWidth / 3) || 0;
            const width = singleWidth * this.step;
            this.computeWidth = width > 0 ? width : 0;
        },
        jumpToRule() {
            const dom = document.querySelector('#activityRule');
            dom.scrollIntoView(true);
            this.$o2o({
                e_c: '面对面邀请',
                e_a: '点击',
                e_n: '活动规则',
            });
        },
        getHomeInfo() {
            f2fHome().then((res) => {
                this.carousel = res.carousel.map(item => (item.length > 4 ? `${item.slice(0, 4)}***` : item));
                this.step = res.step;
                this.remain = res.remain;
                this.invitationCode = res.invitationCode || this.code;
                // 登录
                if (!this.invitationCode) {
                    doLogin('location.reload');
                    return;
                }
                // 调用红包接口
                this.f2fRefresh();
                this.autoOpenTimer();
                // 启动兜底红包功能
                this.traverseRemain();
                // 动态计算不同步骤的宽度
                this.computeDomWidth();
            }).catch(() => {
                this.carousel = [];
            });
        },
        autoOpenTimer() {
            // 生命周期内轮询刷新二维码
            const time = 2 * 60 * 1000;
            this.timer = setInterval(() => {
                this.f2fRefresh();
            }, time);
        },
        f2fRefresh() {
            refresh().then((res) => {
                this.token = res;
                if (!this.qr) {
                    this.qr = new QRious({ // eslint-disable-line
                        element: this.$refs.qr,
                        size: 230,
                    });
                }
                this.qr.value = `https://feed.browser.miui.com/mobile-v2/act/invitation/share?shareId=${this.invitationCode}&share=face2face&token=${this.token}`;
            }).catch(() => {
                this.$error();
            });
        },
        scanQrCode(data) {
            // 正在扫码的优先展示
            console.log('扫码通知');
            console.log(data);
            if (data.detail) {
                const key = data.detail.message;
                this.remain.unshift(key);
                this.traverseRemain();
            }
        },
        traverseRemain() {
            if (!this.remain.length) {
                return;
            }
            //  发送请求
            this.key = this.remain.shift();
            this.ShowReceivePacketModal = true;
            this.$o2o({
                e_c: '面对面邀请',
                e_a: '曝光',
                e_n: '开红包页',
            });
        },
        getf2fPacket() {
            f2fPacket({
                key: this.key,
            }).then((res) => {
                if (res.data.status === 200) {
                    this.coin = res.data.data.coin;
                    this.closeModal('ShowReceivePacketModal');
                    this.ShowGoldIconModal = true;
                    this.$o2o({
                        e_c: '面对面邀请',
                        e_a: '曝光',
                        e_n: '红包详情页',
                    });
                    this.bounceLeave = false;
                } else if (res.data.status === 500) {
                    const result = res.data.data;
                    if (result && result.code) {
                        this.closeModal('ShowReceivePacketModal');
                        this.showToastModal = true;
                        this.toastMsg = {
                            title: result.title,
                            hint: result.hint,
                            msg: result.msg,
                        };
                    }
                } else {
                    this.$error();
                }
            }).catch(() => {
                this.$error();
            });
        },
        goRecordBtn() {
            // 登录
            if (!this.invitationCode) {
                doLogin('location.reload');
                return;
            }
            this.$o2o({
                e_c: '面对面邀请',
                e_a: '点击',
                e_n: '领取记录',
                e_x: { Page: '首页' },
            });
            this.goRecord();
        },
        depositAccount() {
            this.$o2o({
                e_c: '面对面邀请',
                e_a: '点击',
                e_n: '领取记录',
                e_x: { Page: '红包页' },
            });
            this.goRecord();
        },
        goRecord() {
            this.$router.push({
                name: 'receivedRecord',
                params: {
                    url: `https://feed.browser.miui.com/mobile-v2/act/invitation/share?shareId=${this.invitationCode}&share=face2face`,
                    code: this.invitationCode,
                    userName: this.userInfo.user_name,
                    pathName: location.pathname,
                },
            });
        },
        closeReceiveModal() {
            this.closeModal('ShowReceivePacketModal');
            setTimeout(() => {
                this.traverseRemain();
            }, 500);
        },
        closeGoldIcon() {
            this.closeModal('ShowGoldIconModal');
            this.traverseRemain();
        },
        isShowNavbar() {
            return isXiangkan();
        },
        closeModal(key) {
            this[key] = false;
        },
        openPacket() {
            // 红包逻辑 bounceLeave控制特效
            this.bounceLeave = true;
            this.getf2fPacket();
        },
        share() {
            // 登录
            if (!this.invitationCode) {
                doLogin('location.reload');
                return;
            }
            this.$o2o({
                e_c: '面对面邀请',
                e_a: '点击',
                e_n: '好友不在身边分享给他',
                e_x: { Page: '首页' },
            });
            const url = `https://feed.browser.miui.com/mobile-v2/act/invitation/share?shareId=${this.invitationCode}&share=face2face`;
            shareCodeTextToWechat(url, this.invitationCode);
        },
    },
};
</script>

