import { inViewport } from './dom';

const throttleTime = 16;
let listenerFlag = true;


function throttle(fn, delay) {
    let flag = true;
    let timer = null;
    // eslint-disable-next-line func-names
    return (...rest) => {
        if (!flag) {
            return;
        }
        flag = false;
        clearTimeout(timer);
        timer = setTimeout(() => {
            fn.apply(this, rest);
            flag = true;
        }, delay);
    };
}


function scrollMonitor(params) {
    const { targetDom, customMonitorFn, callback, threshold, keepAlive } = params;
    if (typeof customMonitorFn === 'function') {
        if (customMonitorFn() && listenerFlag) {
            // callback 为可选函数
            if (callback) {
                callback();
            }
            if (!keepAlive) {
                listenerFlag = false;
            }
        }
    } else if (inViewport(targetDom, threshold) && listenerFlag) {
        callback();
        listenerFlag = false;
    }
}

/** *
 * dom: 滑动的dom
 * targetDom: 需要监听的目标dom
 * callback: 当满足条件的时候 回调函数
 * delay: 延时
 * threshold: 间距 inViewport所需
 * customMonitorFn: 自定义监控函数  未传入的话 使用inViewport
 * customMonitorFn: 自定义监控函数  未传入的话 使用inViewport
 */
function monitorDom(params) {
    const { delay } = params;
    // eslint-disable-next-line consistent-return
    return throttle(() => {
        scrollMonitor(params);
    }, delay);
}

let sameMemory = null;


export function listenScrollMointorDomInView(params) {
    const { dom } = params;
    const paramsObj = Object.assign(params, {
        delay: params.delay || throttleTime,
    });
    sameMemory = monitorDom(paramsObj);
    dom.addEventListener(
        'scroll',
        sameMemory,
    );
}

export function removeScrollMointorDomInView(params) {
    const { dom } = params;
    listenerFlag = false;
    dom.removeEventListener(
        'scroll',
        sameMemory,
    );
}
